export function SearchHeader() {
  return (
    <div className="w-full sm:w-[700px] h-auto sm:h-[60px] bg-zinc-300 mx-auto mt-64 mb-2 border border-solid border-zinc-800 flex flex-col sm:flex-row justify-center items-center gap-2 p-2">
      <select
        name="city"
        id="city-select"
        className="font-roboto text-xl sm:text-base w-[80%] sm:w-[100px] mx-auto bg-zinc-300 focus:outline-0"
      >
        <option value="">City</option>
        <option value="petersburg">Санкт-Петербург</option>
        <option value="samara">Самара</option>
        <option value="perm">Пермь</option>
        <option value="novosibirsk">Новосибирск</option>
      </select>

      <div className="hidden sm:block w-0 h-10 border-s border-solid border-black mx-auto"></div>

      <select
        name="district"
        id="district-select"
        className="font-roboto text-xl sm:text-base w-[80%] sm:w-[100px] mx-auto bg-zinc-300 focus:outline-0"
      >
        <option value="">District</option>
        <option value="1">1</option>
        <option value="2">2</option>
        <option value="3">3</option>
        <option value="4">4</option>
      </select>

      <div className="hidden sm:block w-0 h-10 border-s border-solid border-black mx-auto"></div>

      <select
        name="rooms"
        id="rooms-select"
        className="font-roboto text-xl sm:text-base w-[80%] sm:w-[100px] mx-auto bg-zinc-300 focus:outline-0"
      >
        <option value="">Rooms</option>
        <option value="1">1</option>
        <option value="2">2</option>
        <option value="3">3</option>
        <option value="4">4</option>
      </select>

      <div className="hidden sm:block w-0 h-10 border-s border-solid border-black mx-auto"></div>

      <select
        name="price"
        id="price-select"
        className="font-roboto text-xl sm:text-base w-[80%] sm:w-[100px] mx-auto bg-zinc-300 focus:outline-0"
      >
        <option value="">Price</option>
        <option value="1">1</option>
        <option value="2">2</option>
        <option value="3">3</option>
        <option value="4">4</option>
      </select>

      <button className="font-roboto text-xl sm:text-base mx-auto bg-gray-800 text-gray-100 w-[80%] sm:w-[100px] h-full">
        Search
      </button>
    </div>
  );
}